<template>
    <div class="referral-heat">
         <button class="back-button" @click="$router.back()">
            <img class="back-button-arrow" src="@/assets/newImages/black_arrow.png"/>
            <p class="back-button-text">{{$localizeFilter('our_promotions')}}</p>
         </button>

         <div class="container">
             <div class="window light">
                <p class="window-title">{{$localizeFilter('referral_heat')}}</p>
                <div class="window-subtitle">{{ dates }}</div>
                <p class="window-text" :style="'margin-top: 26px; margin-bottom: 0;'">
                    {{$localizeFilter('referral_heat_text_1')}}
                </p>
                <ul :style="'margin-top: 4px'">
                    <li v-for="(bonus, idx) in bonuses" v-bind:key="idx">
                        <div class="referral-heat-list-cell-text" v-html="$localizeFilter('referral_heat_list_cell_text', [bonus.invited, bonus.bonus])"></div>
                    </li>
                </ul>
                <p class="window-text" :style="'margin-bottom: 26px;'">
                    {{$localizeFilter('referral_heat_text_2')}}
                </p>

                <button class="basic-button" @click="$router.push('/cabinet/bonusAccount')">{{$localizeFilter('view_bonus_account')}}</button>
             </div>
         </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        startDate: {
            reqired: true
        },
        endDate: {
            reqired: false
        }
    },
    data: () => ({
        bonuses: [
            {invited: '<10', bonus: 1},
            {invited: 10, bonus: 5},
            {invited: 20, bonus: 10},
            {invited: 30, bonus: 12},
            {invited: 40, bonus: 18},
            {invited: 60, bonus: 20},
            {invited: 80, bonus: 28},
            {invited: 100, bonus: 37}
        ]

    }),
    computed: {
        dates(){
            if(this.endDate){
                return this.$localizeFilter('date_from_to', [this.$dateTimeFilter(this.startDate, 'custom', 'dd.mm.yyyy'), this.$dateTimeFilter(this.endDate, 'custom', 'dd.mm.yyyy')]);
            }
            return this.$localizeFilter('date_from', [this.$dateTimeFilter(this.startDate, 'custom', 'dd.mm.yyyy')]);
        }
    },
    methods: {

    },
    setup() {
        
    },
})
</script>

